  box-shadow: 0 0 1em rgba(0, 0, 0, 0.5);
  scale: 1;
}
.events-container .event-links .event-card .description,
.gallery-container .event-links .event-card .description {
  width: 100%;
  padding: 1em;
  text-align: left;
  height: fit-content;
}
.events-container .event-links .event-card .description h2,
.gallery-container .event-links .event-card .description h2 {
