}
.events-container .event-links .event-card .read-more,
.gallery-container .event-links .event-card .read-more {
  color: #fff;
  float: right;
}
.socials-container {
  display: flex;
  gap: 2em;
  justify-content: center;
  flex-wrap: wrap;
}
.socials-container a:active,
.socials-container a:hover,
.socials-container a:focus {
  text-decoration: underline;
}
.socials-container .social a {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 0.5em;
  margin-bottom: 0.5em;
}
.socials-container .social a .icon {
  width: 1.5em;
  aspect-ratio: 1;
}
.theme-text-color {
  color: gold !important;
}
